@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,700i&display=swap);
html body {
  padding: 0;
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.hero {
  visibility: hidden;
  height: 100vh;
}
.hero .container {
  width: 1440px;
  min-width: 1440px;
  margin: 0 auto;
}
.hero .container .hero-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 24px;
  height: 100vh;
}
.hero .container .hero-inner .hero-content {
  width: 50%;
}
.hero .container .hero-inner .hero-content .hero-content-inner {
  width: 400px;
  color: #323232;
  margin: 0 auto;
}
.hero .container .hero-inner .hero-content .hero-content-inner h1 {
  font-weight: 500;
  font-size: 36px;
  margin-bottom: 24px;
}
.hero .container .hero-inner .hero-content .hero-content-inner h1 .hero-content-line {
  margin: 0;
  height: 44px;
  overflow: hidden;
}
.hero .container .hero-inner .hero-content .hero-content-inner p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  padding-right: 48px;
  margin-bottom: 56px;
}
.hero .container .hero-inner .hero-content .hero-content-inner .btn-row {
  position: relative;
}
.hero .container .hero-inner .hero-content .hero-content-inner .btn-row:before {
  position: absolute;
  content: "";
  width: 56px;
  height: 1px;
  background: #dfdfdf;
  top: 50%;
  left: -68px;
}
.hero .container .hero-inner .hero-content .hero-content-inner .btn-row button {
  background: none;
  border: none;
  font-size: 13px;
  padding: 0;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.7px;
  display: flex;
  align-items: center;
  position: relative;
}
.hero .container .hero-inner .hero-content .hero-content-inner .btn-row button:focus {
  outline: none;
}
.hero .container .hero-inner .hero-content .hero-content-inner .btn-row button .arrow-icon {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #007fff;
  height: 48px;
  width: 48px;
  border-radius: 100px;
  margin-left: 16px;
  left: 64px;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}
.hero .container .hero-inner .hero-images {
  width: 50%;
  height: 100vh;
  position: relative;
}
.hero .container .hero-inner .hero-images .hero-images-inner .hero-image {
  position: absolute;
  overflow: hidden;
}
.hero .container .hero-inner .hero-images .hero-images-inner .hero-image.trees {
  top: 0;
  right: 0px;
  width: 45%;
  height: 50%;
}
.hero .container .hero-inner .hero-images .hero-images-inner .hero-image.ice {
  bottom: 24px;
  left: 0px;
  width: 52%;
  height: 65%;
}
.hero .container .hero-inner .hero-images .hero-images-inner .hero-image img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
}
